@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
body{
    margin: 0px;
}
.header{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 60px;
    margin: 0px;
    background: radial-gradient(50% 28800% at 50% 68.33%, #4E86E4 0%, #2F68C8 100%);
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
}
.header-logo{
    margin-left: 20%;
    padding-top: 21px;
}
.add-user{
    margin-left: 35%;
    padding-top: 20px;
    text-decoration: none;
    color: silver;
}
.list-ofUsers{
    margin-left: 3%;
    padding-top: 20px;
    text-decoration: none;
    color: silver;
}
.active{
    color: white;
}

.body{
    margin: 0px;
    padding: 0px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #657C9A;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.form{
    display: flex;
    flex-direction: column;
    align-content: flex-end;
}
.avatar{
    margin-right: 15%;
}
.btn-submit{
border: 0px;
height: 40px;
background: #5E97F3;  
color: white;
padding: 0px 15px;
margin-top: 150px;
}
.input{
    width: 400px;
    height: 40px;
    background: #FFFFFF;
}
.inputMargin{
    margin-bottom: 40px;
}

.AccountForm{
display: flex;
flex-direction: row;
justify-content: center;
align-content: flex-end;
}
.firstColumn{
display: flex;
flex-direction: column;
margin-right: 10%;
}
.secondColumn{
flex-direction: column;
}
.radio{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
.radio>*:first-child{
    margin-right: 30px;
}
.btn-profile{
    border: 0px;
    height: 40px;
    background: #5E97F3;  
    color: white;
    padding: 0px 15px;
}
.input-profile{
    width: 300px;
    height: 40px;
    background: #FFFFFF;
}
.contactsForm{
    display: flex;
    flex-direction: row;
    justify-content: center;
    }
.select{
    width: 300px;
}
.lang{
    width: 300px;
}
.btn-contacts{
    border: 0px;
    height: 40px;
    background: #5E97F3;  
    color: white;
    padding: 0px 15px;
}
.CapabilitiesForm{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.select{
    margin-bottom: 200px;
}
.textarea{
    width: 300px;
    height: 100px;
}
